/* The switch - the box around the slider */
.wcb-toggle__switch {
  position: relative;
  display: inline-block;
  width: 60px;
  height: 34px;
  /* Hide default HTML checkbox */
}
.wcb-toggle__switch input {
  opacity: 0;
  width: 0;
  height: 0;
}

/* The slider */
.wcb-toggle__slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #ccc;
  -webkit-transition: 0.4s;
  transition: 0.4s;
}
.wcb-toggle__slider::before {
  position: absolute;
  content: "";
  height: 26px;
  width: 26px;
  left: 4px;
  bottom: 4px;
  background-color: white;
  -webkit-transition: 0.4s;
  transition: 0.4s;
}

input:checked + .wcb-toggle__slider {
  background-color: #2196f3;
}

input:focus + .wcb-toggle__slider {
  box-shadow: 0 0 1px #2196f3;
}

input:checked + .wcb-toggle__slider:before {
  -webkit-transform: translateX(26px);
  -ms-transform: translateX(26px);
  transform: translateX(26px);
}

/* Rounded sliders */
.wcb-toggle__slider.round {
  border-radius: 9999px !important;
}
.wcb-toggle__slider.round:before {
  border-radius: 9999px !important;
}